import React from 'react'
import google from "../images/webimages/google1.jpg"
import fb from "../images/webimages/facebook1.jpg"
import instagram from "../images/webimages/instagram1.jpg"
import pinterest from "../images/webimages/pinterest1.jpg"
import youtube from "../images/webimages/youtube1.jpg"
import heart from "../images/webimages/heart.jpg"
const Workingwith = () => {
    return (
        <div className="container workingwith">

            <div className="row  ">

                <div className="row workingwithtext">


                    <div className="col-md-12  text-center">

                        <h3 className="text-center">  Platforms We 
                        
                        <span><img src={heart} className=" ms-2 text-center" /></span>  Working With</h3>
                    </div>
                </div>
                <div className="row  align-items-center justify-content-center  workingwithicons ">
                <div className="col " >
                    <img src={google} />
                </div>
                <div className="col ">
                    <img src={fb} />
                </div>
                <div className="col " >
                    <img src={instagram} />
                </div>
                <div className="col  ">
                    <img src={youtube} />
                </div>
                <div className="col ">
                    <img src={pinterest} />
                </div>
            </div>
            </div>

        </div>
    )
}

export default Workingwith
